<template>
 <div class="wrapper" v-if="!isItemsLoading">
   <span class="title">General terms and definitions in English</span>
    <div class="list">
      <div class="legend">
        <span>Term in eng</span>
        <span>Term in rus</span>
      </div>
      <div class="list-item" v-for="item in this.items" :key="item">
        <span>
          {{ item.name }}
        </span>
        <span>
          {{ item.name_rus }}
        </span>
      </div>
    </div>
 </div>
 <div class="loading" v-else>Идет загрузка...</div>
</template>

<script>
import termsAPI from "@/services/termsAPI";
export default {
  name: "EngTerms",
  data() {
    return {
      items: [],
      isItemsLoading: true,
      page: 1
    }
  },
  methods: {
    async fetch() {
      this.isItemsLoading = true
      this.items = [...JSON.parse(JSON.stringify(await termsAPI.getTermsList('eng', this.page))).rows]
      this.isItemsLoading = false
    },
    async append() {
      this.page++
      const buffer = [...JSON.parse(JSON.stringify(await termsAPI.getTermsList('eng', this.page))).rows]
      for(let item of buffer) {
        this.items.push(item)
      }
    },
    async scroll() {
      const itemsList = document.querySelector('.list')
      itemsList.addEventListener('scroll', async () => {
        if (itemsList.scrollTop + itemsList.clientHeight >= itemsList.scrollHeight) {
          await this.append()
        }
      })
    }
  },
  async mounted() {
    await this.fetch()
    await this.scroll()
  }
}
</script>

